:root {
  --main-color: #00acdc;
}

body {
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.logo {
  margin: 10px 0 10px 50px;
}

.header__nav {
  margin-right: 50px;
}

.nav__list {
  display: flex;
}

.nav__item {
  height: 60px;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  line-height: 60px;
  display: flex;
}

.nav__link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  text-transform: uppercase;
  border-bottom: 4px solid var(--main-color);
  border-radius: 8px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

